import React from "react";
import Card from "react-bootstrap/Card";
import Button from "react-bootstrap/Button";
import { CgWebsite } from "react-icons/cg";
import { BsGithub } from "react-icons/bs";

function ProjectCards(props) {
  return (
    <Card className="project-card-view">
      <Card.Img variant="top" style={{padding: 20}} src={props.imgPath} alt="card-img" />
      <Card.Body>
        <Card.Title>{props.title}</Card.Title>
        <Card.Text style={{ textAlign: "left" }}>
          {props.description}
        </Card.Text>
        <div style={{display: "flex", justifyContent: "space-between"}}>
        <Card.Text style={{ fontWeight: 700 }}>{props.status}</Card.Text>
        { props.isPaper && props.paperLink && (
          <Button variant="primary" href={props.paperLink} target="_blank">
          {/* <BsGithub /> &nbsp; */}
          {"Paper"}
        </Button>
        )}
        
        {"\n"}
        {"\n"}

        {/* If the component contains Demo link and if it's not a Blog then, it will render the below component  */}

        { props.isVideo && props.videoLink && (
          <Button
            variant="primary"
            href={props.videoLink}
            target="_blank"
            style={{ marginLeft: "10px" }}
          >
            {/* <CgWebsite /> &nbsp; */}
            {"Video"}
          </Button>
        
        )}
        </div>
      </Card.Body>
    </Card>
  );
}
export default ProjectCards;
